import React, { Component } from 'react';

export default class CursoSpring extends Component {

    constructor(props) {
        super(props);
        this.state = {
            texto: "",
            bgColor: ""
        }
    }

    cambiarColor = (e) => {
        this.setState({
            bgColor: "#00A8CC"
        })
    }
    mostrarTexto  = (e) => {
        this.setState({
            texto : "contenido añadido"
        })
    }

    render() {
        return (
            <section className="card-container">
                <div className="card"  style={{ backgroundColor: this.state.bgColor }}>
                    <div className="card__image">
                        <img src="img/spring-logo2.svg" alt="spring" />
                    </div>
                    <div className="card__textContent">
                        <div className="card__title">
                            <div className="card__title-main">Java con Spring framework</div>
                            <div className="card__title-sub">
                                Introducción {this.state.texto}
                  </div>
                        </div>
                        <div className="card__content">
                            <p>
                                Spring es un framework para el desarrollo de aplicaciones y contenedor de inversión de control, de código abierto para la plataforma Java.
                  </p>
                            <p>
                                Spring es un framework para el desarrollo de aplicaciones y contenedor de inversión de control, de código abierto para la plataforma Java.
                  </p>
                        </div>

                        <button  onClick={this.cambiarColor}> Cambiar color
                        </button>
                        
                        <button type="button"
                            onClick={(e) => {
                                e.preventDefault();
                                window.location.href = 'https://es.wikipedia.org/wiki/Spring_Framework';
                            }} className="btn btn-info">Ver más</button>
                            <button  onClick={this.mostrarTexto}> Modifica el DOM
                        </button>
                    </div>
                </div>
            </section>
        );
    }



}