import React, { Component } from 'react';

export default class CursoReact extends Component {

    constructor(props) {
        super(props);
        this.state = {
            bgColor: ""
        }
    }

   


    boxClick = (e) => {
        this.setState({
            bgColor: "#FF5733"
        })
    }
    mostrarTexto  = (e) => {
        this.setState({
            texto : "contenido añadido"
        })
    }

    handleClick() {
        console.log('this is:', this);
        alert("Botón presionado");
    }
    render() {
        return (
            <section className="card-container">
                <div className="card" style={{ backgroundColor: this.state.bgColor }}>
                    <div className="card__image">
                        <img src="img/react-logo.png" alt="react" />
                    </div>
                    <div className="card__textContent">
                        <div className="card__title">
                            <div className="card__title-main"> Curso de React JS </div>
                            <div className="card__title-sub">
                                Introducción  {this.state.texto}
                  </div>
                        </div>
                        <div className="card__content">
                            <p>
                                React (también llamada React.js o ReactJS) es una biblioteca Javascript de código abierto diseñada para crear interfaces de usuario con el objetivo de facilitar el desarrollo de aplicaciones en una sola página. Es mantenido por Facebook y la comunidad de software libre. Han participado en el proyecto más de mil desarrolladores diferentes.
                  </p>
                            <p>
                                React intenta ayudar a los desarrolladores a construir aplicaciones que usan datos que cambian todo el tiempo. Su objetivo es ser sencillo, declarativo y fácil de combinar.
                  </p>
                        </div>

                        <button className="card_button button"
                            onClick={this.boxClick}> Cambiar color
                        </button>

                        <button type="button"
                            onClick={(e) => {
                                e.preventDefault();
                                window.location.href = 'https://es.wikipedia.org/wiki/React';
                            }} className="btn btn-info">Ver más</button>
                          <button  onClick={this.mostrarTexto}> Modifica el DOM
                        </button>
                    </div>
                </div>
            </section>
        );
    }

   
}